.testimonials {
    font-family: var(--primaryFont);
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    overflow: hidden;
}


/* SLIDER STYLES */
.slick-slider {
    height: 100% !important;
    width: 100%;
}


.slick-initialized {
    overflow: hidden;
}

.slick-list {
    position: relative;
    display: block;
    overflow: hidden;
    margin: 0;
    padding: 0;
    width: 100%;
}
.slick-dots {
    position: relative !important;
    bottom: 1rem !important;

}


.testimonials--header h1{
    font-style: normal;
    font-weight: bold;
    font-size: 3.5rem;
    text-align: center;
}

.testimonials--body {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    width: 95%;
    margin-top: 1rem;

    position: relative;

}

.testimonials--body .quote{
    font-size: 4rem;
    position: absolute;
    z-index: 10;

    -webkit-transform: translateY(50%);

            transform: translateY(50%);

    opacity: 0.5;
}

.testimonials--body button{
    position: absolute;
    top: 50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    width: 60px;
    height: 60px;
    border-radius: 50%;

    -webkit-appearance: none;

            appearance: none;
    outline: none;
    border: none;

    display: flex;
    align-items: center;
    justify-content: center;

    font-size: 1.6rem;
}
.testimonials--body .prevBtn{
    left: 5%;
    cursor: pointer;
}
.testimonials--body .nextBtn{
    right: 5%;
    cursor: pointer;
}

.testimonials--slider {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    width: 70%;
}

.single--testimony {
    padding: 2rem;
